.myModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background-color: rgba(0, 0, 0, 0.418);
    z-index: 3;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}

.myModalContent {
    padding: 25px;
    background-color: white;
    border-radius: 12px;
    min-width: 260px;
}