.header {
    display: flex;
    justify-content: center;
}
.header a {
    font-family: Lato, Helvetica, Arial, sans-serif;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #222222;
    text-decoration: none;
    padding: 15px;  
}

.header a:hover {
    color: #be9656;
}